:root {
  --navOffset: 80px;
  --availScreen: calc(100vh - var(--navOffset));
  --background-white: rgb(252, 252, 252);
  --background-white-t: rgb(252, 252, 252, 0.5);
}

.App {
  text-align: center;
  font-family: "Manrope", sans-serif;
}

.App-main {
  margin-top: 80px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
